.homeContainer {
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 500px;
  max-height: 1280px;
  background-color: rgb(255, 255, 255);
  max-width: 500px;
  align-items: center;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  justify-content: space-between;

  p {
    margin-top: 16px;
  }

  h3 {
    color: black;
    margin-top: 16px;
  }

  #button {
    margin-bottom: 16px;
  }
}
